@charset "UTF-8";
@import "../../../style/theme/default/var.css";

.el-button+.el-button {
  margin-left: var(--theme-margin-base);
}
.el-popover {
  min-width: 80px;
  .tmp-list {
    padding: 0;
    margin: 0;
    li {
      list-style: none;
      &:not(:last-child) {
        border-bottom: 1px solid var(--border-color-base);
       }
    }
  }
}

@component-namespace portrait {
  @b instructions {
    display: none;
    padding: var(--theme-padding-base);
    margin-bottom: var(--theme-margin-base);
    background-color: var(--theme-background-color-primary);
    p {
      margin-bottom: 0;
    }
    p:not(:last-child) {
      margin-bottom: 5px;
    }
  }
  @b form {
    padding: var(--theme-padding-base) var(--theme-padding-base) var(--theme-padding-small);
    margin-bottom: var(--theme-margin-base);
    background-color: var(--theme-background-color-primary);
  }
  @b panel {
    padding: 0 var(--theme-padding-base) var(--theme-padding-large);
    background-color: var(--color-white);
    border: 1px solid var(--border-color-base);
    margin-bottom: var(--theme-margin-base);
    /*&:last-child {*/
       /*margin-bottom: 0;*/
     /*}*/
    overflow: hidden;
    .el-form-item__label {
      padding: 9px 0;
    }
    @e title{
      height:50px;
      line-height: 30px;
      padding: var(--theme-padding-base) 0;
      & > span{
        position: relative;
        display: inline-block;
        padding:0 var(--theme-padding-small);
        &:before {
          content: ' ';
          position: absolute;
          top: -10px;
          left: 5px;
          right: 5px;
          height: 4px;
          background-color: var(--theme-color-primary);
        }
      }
      .el-form-item__label {
        float: left;
      }
      .el-form-item {
        margin-right: 0;
      }
      .tmp-panel__options a {
        cursor: pointer;
      }
    }
    @e body {
      &.special-echarts {
        position: relative;
        height: 284px;
        /*overflow: hidden;*/
        .echarts {
          position: absolute;
          top: 56%;
          left: 63.5%;
          -webkit-transform: translate(-50%,-50%);
          -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
          height: 300px;
          width: 500px;
        }
      }
      &.small-echarts {
        .echarts {
          width: 100%;
          height: 284px;
        }
      }
      .echarts, .wordCloud {
        height: 284px;
        width: 100%;
      }
      .touchmode-lg.echarts {
        height: 180px;
      }
      .touchmode-sm.echarts {
        height: 70px;
      }
      &  .touchmode-item{
        display: inline-block;
        position: relative;
        font-size: var(--theme-font-size-small);
        text-align:left;
        padding-left:28px;
        & i{
          position: absolute;
          /*  top:-48px;
          left: 50%;*/
          left:0;
          top:50%;
          margin-top:-16px;
          font-size:23px;
          color: var(--theme-border-color-primary);
        }
      }
    }
  }
}



